import React, { useState, useEffect } from 'react'
import { Input, Button, Upload, Modal } from 'antd';
import add from '../../../image/添加导航信息图标.png'
import sort from '../../../image/排序图标.png'
import del from '../../../image/删除图标.png'
import img from '../../../image/上传图标图标.png'
import { ExclamationCircleOutlined } from '@ant-design/icons'
import { connect } from 'react-redux'
import { getTable, updateTables, addTable, delTable } from '../../../store/actionCreator'
import { ReactSortable } from "react-sortablejs";

const CM = (props) => {
    let pageid = props.match.params.pageid;
    const [state, setState] = useState(props.table);
    useEffect(() => {
        props.dispatch(getTable(pageid));
    }, [pageid])
    useEffect(() => {
        setState(props.table);
    }, [props.table])

    // console.log(props.table);

    const [handle, setHandle] = useState('false');
    const [fd1, setFd1] = useState('');
    const [fd2, setFd2] = useState('');
    const [fd3, setFd3] = useState('');
    const [fd4, setFd4] = useState('');
    function confirm(tableid) {
        Modal.confirm({
            title: 'Confirm',
            icon: <ExclamationCircleOutlined />,
            content: '删除此项？',
            okText: '确认',
            cancelText: '取消',
            onOk: () => {
                props.dispatch(delTable(tableid));
                props.dispatch(getTable(pageid));
                setState(props.table);
            }
        });
    }
    const p = {
        name: 'img',
        action: '/uploadmenulogo',
        headers: {
            authorization: 'authorization-text',
        },
        onChange(info) {
            if (info.file.status !== 'uploading') {
                console.log(info.file, info.fileList);
            }
            if (info.file.status === 'done') {
                console.log(info.file.response)
                setFd3(info.file.response)
            } else if (info.file.status === 'error') {
                console.log(info.file.name)
            }
        }
    };

    return (
        <div style={{}}>
            {/* 标题栏 */}
            <div style={{ height: 50, backgroundColor: 'rgb(101,153,255)' }}>
                <div style={{ width: global.tpa1(1611), margin: '0 auto', paddingTop: 5 }}>
                    <div style={{ ...title, width: '8.5%' }}>排序</div>
                    <div style={{ ...title, width: '12%' }}>标题</div>
                    <div style={{ ...title, width: '31.5%' }}>简介</div>
                    <div style={{ ...title, width: '9%' }}>上传图标</div>
                    <div style={{ ...title, width: '23%' }}>链接</div>
                    <div style={{ ...title, width: '6.5%' }}>提交</div>
                    <div style={{ ...title, width: '7%' }}>删除</div>
                </div>
            </div>
            <div style={{ width: global.tpa1(1611), backgroundColor: '#fff', margin: '0 auto' }}>
                <div style={{ paddingTop: 15 }}>
                    {/* 添加图标 */}
                    <div style={{ width: '8.5%', paddingBottom: 15, display: 'inline-block' }}>
                        <img src={add} style={{ width: 20, margin: '0 auto', display: 'block' }}
                            onClick={() => setState([...state, {}])}
                        />
                    </div>
                    <div style={{ float: 'right', display: 'inline-block' }}>
                        <Button style={sortBtn}
                            onClick={() => {
                                setHandle('handle'); 
                            }}
                        >排序</Button>
                        <Button style={sortBtn}
                            onClick={() => {
                                setHandle('false')
                                const sortTables = [];
                                state.map((item, i) => {
                                    sortTables.push({ tableid: item.tableid, sortid: i + 1 })
                                })
                                sortTables.sort((a, b) => a.menuid - b.menuid)
                                console.log(sortTables)
                                props.dispatch(updateTables(sortTables))
                            }}
                        >完成</Button>
                    </div>
                </div>
                <ReactSortable
                    list={state}
                    setList={setState}
                    group="groupName"
                    animation={200}
                    delayOnTouchStart={true}
                    delay={2}
                    handle={'.handle'}
                >
                    {
                        state.map((item) => {
                            return item.tableid
                                ? <div style={{ ...list, paddingLeft: global.tpa2(44) }} key={item.id}>
                                    <img src={sort} style={{ width: 30, marginRight: global.tpa2(44), position: 'relative', top: -12 }} className={handle} />
                                    <div style={{ ...form, width: global.tpa2(191), paddingTop: 3, overflowY: 'auto' }}><p>{item.title}</p></div>
                                    <div style={{ ...form, width: global.tpa2(496), paddingTop: 3, overflowY: 'auto' }} ><p>{item.brief}</p></div>
                                    <Button disabled
                                        icon={<img src={img} style={{ width: 18, marginRight: 3, display: 'inline-block' }} />}
                                        style={{ ...form2, width: global.tpa2(141), position: 'relative', top: -10 }}
                                    >已上传</Button>
                                    <div style={{ ...form, width: global.tpa2(362), paddingTop: 3, overflowY: 'auto' }}><p>{item.link}</p></div>
                                    <Button style={{ ...form2, width: global.tpa2(82), textAlign: 'center', backgroundColor: 'rgb(101,153,255)', color: '#fff', position: 'relative', top: -10 }} disabled>
                                        已提交
                                    </Button>
                                    <img src={del}
                                        style={{ width: 25, marginLeft: global.tpa2(44), position: 'relative', top: -12 }}
                                        onClick={() => {
                                            confirm(item.tableid)
                                        }}
                                    />
                                </div>
                                : <div style={{ ...list, paddingLeft: global.tpa2(44) }} key={item.id}>
                                    <img src={sort} style={{ width: 30, marginRight: global.tpa2(44) }} className='false' />
                                    <Input
                                        onChange={e => setFd1(e.target.value)}
                                        style={{ ...form2, width: global.tpa2(191), }} />
                                    <Input
                                        onChange={e => setFd2(e.target.value)}
                                        style={{ ...form2, width: global.tpa2(496) }} />
                                    <div style={{ display: 'inline-block', margin: '0 0.6%', width: global.tpa2(141), backgroundColor: '#abc', padding: 0, }}>
                                        <Upload {...p}
                                            listType='none'
                                        >
                                            <Button
                                                icon={<img src={img} style={{ width: 18, marginTop: -5, marginRight: 3 }} />}
                                                // style={{ ...form, width: global.tpa2(141) }}
                                                style={{ width: '100%', margin: 0 }}
                                            >已上传</Button>
                                        </Upload>
                                    </div>
                                    <Input
                                        onChange={e => setFd4(e.target.value)}
                                        style={{ ...form2, width: global.tpa2(362) }} />

                                    <Button style={{ ...form2, width: global.tpa2(82), textAlign: 'center', backgroundColor: 'rgb(101,153,255)', color: '#fff' }}
                                        onClick={() => {
                                            props.dispatch(addTable(pageid, fd1, fd2, fd3, fd4, props.table.length + 1))
                                        }}
                                    >提交</Button>
                                    <img src={del} style={{ width: 25, marginLeft: global.tpa2(44) }}
                                        onClick={() => {
                                            // console.log(item.tableid)
                                            setState(state.slice(0, state.length - 1))
                                        }}
                                    />
                                </div>
                        })
                    }
                </ReactSortable>
            </div>
        </div >
    )
}

const list = {
    backgroundColor: '#F2F2F2',
    marginBottom: 5,
    height: 57,
    paddingTop: 14,
    borderRadius: 5
}
const title = {
    display: 'inline-block',
    color: '#fff',
    fontStyle: 'SourceHanSansCN',
    fontWeight: 'bold',
    textAlign: 'center',
    lineHeight: 3
}
const form = {
    margin: '0 0.6%',
    height: 30,
    padding: 0,
    display: 'inline-block',
    backgroundColor: '#fff',
    border: '1px solid #ccc',
    boxSizing: 'border-box'
}
const form2 = {
    margin: '0 0.6%',
    height: 30,
    padding: 0,
}
const sortBtn = {
    display: 'inline-block',
    backgroundColor: 'rgb(101,153,255)',
    color: '#fff',
    marginLeft: 10
}
const mapStateToProps = (state) => ({
    login: state.reducer.login,
    table: state.reducer.table
})

export default connect(mapStateToProps)(CM)