const defaultStore = {
    login: false,
    menus: [],
    table: [],
    fls: [],
    code: null,
    codetext: null
}

const reducer = (state = defaultStore, action) => {
    switch (action.type) {
        case 'MENUS':
            return Object.assign({}, state, {
                menus: action.menus
            });
        case 'TABLE':
            return Object.assign({}, state, {
                table: action.table
            })
        case 'FRIENDLINK':
            return Object.assign({}, state, {
                fls: action.fls
            })
        case 'LOGIN':
            return Object.assign({}, state, {
                login: action.login
            })
        case 'CODE':
            return Object.assign({}, state, {
                code: action.code,
                codetext: action.text
            })
        default:
            return state;
    }
    
}

export default reducer