import React, { useState, useEffect } from 'react'
import { Layout, Menu, Modal, Input, Button, Upload } from 'antd';
import { Switch, Route, Link, Redirect } from 'react-router-dom'
import './index.css';
import logo from '../../image/运营文件夹logo.png'
import tit1 from '../../image/内容管理图标.png'
import tit2 from '../../image/友情链接图标.png'
import tit3 from '../../image/版权信息图标.png'
import sort from '../../image/分类菜单排序图标.png'
import edit from '../../image/分类菜单编辑图标.png'
import del from '../../image/分类菜单删除图标.png'
import addMenul from '../../image/添加分类图标.png'
import img from '../../image/上传图标图标.png'
import { ExclamationCircleOutlined } from '@ant-design/icons'
import ContentManagement from './contentmanagement'
import FriendLinkfrom from './friendlink'
import Modal2 from './Modal';
import { ReactSortable } from "react-sortablejs";
import { connect } from 'react-redux'
import { getMenus, updateMenus, delMenu, updateMenuName, addMenu, updateCp } from '../../store/actionCreator'
const { Content, Sider } = Layout;
const { SubMenu } = Menu;

const Home = (props) => {
    useEffect(() => {
        let token = localStorage.getItem('token');
        let test = props.location.state?props.location.state.test:true; 
        console.log(test)
        if(test){
            fetch('/islogin', {
                method: 'POST',
                'Content-Type': 'application/json',
                body: JSON.stringify({
                    token: token,
                })
            })
            .then(res=>res.json())
            .then(res=>{
                console.log(res)
                if(!res.msg){
                    props.history.replace('/login')
                }
            })      
        }
    }, [])
    const [menus, setMenus] = useState([]);
    useEffect(() => {
        props.dispatch(getMenus());
    }, [])
    useEffect(() => {
        setMenus(props.menus);
    }, [props.menus])
    const [selected, setSelected] = useState(null);
    const [visible, setVisible] = useState(false);
    const [visible2, setVisible2] = useState(false);
    const [visible3, setVisible3] = useState(false);
    const [handle, setHandle] = useState('false');
    const [newMenu, setNewMenu] = useState('');
    const [menuName, setMenuName] = useState(null);
    const [menuLogo, setMenuLogo] = useState(null);
    const [newcp, setNewcp] = useState(null);
    function confirm() {
        Modal.confirm({
            title: 'Confirm',
            icon: <ExclamationCircleOutlined />,
            content: '删除此项？',
            okText: '确认',
            cancelText: '取消',
            onOk: () => {
                props.dispatch(delMenu(selected))
                props.history.push('/home/cm/'+props.menus[0].pageid)
                setSelected(props.menus[0].menuid)
            }
        });
    }
    const p = {
        name: 'img',
        action: '/uploadmenulogo',
        headers: {
            authorization: 'authorization-text',
        },
        onChange(info) {
            if (info.file.status !== 'uploading') {
                console.log(info.file, info.fileList);
            }
            if (info.file.status === 'done') {
                console.log(info.file.response)
                setMenuLogo(info.file.response)
            } else if (info.file.status === 'error') {
                console.log(info.file.name)
            }
        }
    };
    return <Layout className='layout'>
        <Sider
            // breakpoint="lg"
            // collapsedWidth="0"
            style={{
                overflow: 'auto',
                height: '100vh',
                position: 'fixed',
                left: 0,
                backgroundColor: 'rgb(42,42,42)'
            }}
        >
            <div className="logo">
                <img src={logo} style={{ width: 85, height: 90, display: 'block', margin: '0 auto', }} />
            </div>
            <Menu theme="dark" mode="inline" defaultSelectedKeys={['3']} style={{ backgroundColor: 'rgb(42,42,42)' }}>
                <SubMenu key="sub1" icon={<img src={tit1} style={menu_img} />} title="内容管理" style={menu}>
                    <div style={{ backgroundColor: 'rgb(42,42,42)' }}>
                        <button style={btn}
                            onClick={() => setHandle('handle')}
                        >排序</button>
                        <button style={btn}
                            onClick={() => {
                                setHandle('false')
                                const sortMenus = [];
                                menus.map((item, i) => {
                                    sortMenus.push({ menuid: item.menuid, pageid: item.pageid, sortid: i + 1 })
                                })
                                sortMenus.sort((a, b) => a.menuid - b.menuid)
                                props.dispatch(updateMenus(sortMenus))
                            }}
                        >完成</button>
                    </div>
                    <ReactSortable
                        list={menus}
                        setList={setMenus}
                        group="groupName"
                        animation={200}
                        delayOnTouchStart={true}
                        delay={2}
                        handle={'.handle'}
                    >
                        {
                            menus.map(item => {
                                return <div
                                    style={{ margin: 0, fontSize: 15, fontWeight: 'normal', letterSpacing: 1, height: 34, backgroundColor: 'rgb(42,42,42)'}}
                                    onClick={() => {
                                        setSelected(item.menuid)
                                        props.history.push('/home/cm/' + item.pageid)
                                    }}
                                >
                                    {
                                        selected === item.menuid
                                            ? <div style={{ backgroundColor: 'rgb(101,153,255)', height: 36, paddingTop: 11 }}>
                                                <img src={sort} style={{ width: 13, marginTop: -2.5, marginRight: 7, marginLeft: 28, cursor: 'move' }}
                                                    className={handle}
                                                />
                                                <span to='/home/cm' style={{ color: '#fff', fontWeight: 'bold', cursor: 'pointer' }}>{item.title + item.menuid}</span>
                                                <img src={edit} style={menu_icon} onClick={() => { setVisible3(true) }} />
                                                <img src={del} style={{ ...menu_icon, width: 15 }} onClick={confirm} />
                                            </div>
                                            : <div style={{ marginLeft: 49, paddingTop: 18 }}>
                                                <span to='/home/cm' style={{ color: '#fff', cursor: 'pointer' }}>{item.title + item.menuid}</span>
                                            </div>
                                    }
                                </div>
                            })
                        }
                    </ReactSortable>
                    <div onClick={() => setVisible2(true)}
                        style={{ height: 54, paddingTop: 15, paddingLeft: 37, color: '#fff', fontWeight: 'normal', fontSize: 16, backgroundColor: 'rgb(42,42,42)', userSelect: 'none', cursor: 'pointer' }}>
                        <img src={addMenul} style={{ width: 12, marginRight: 5, marginTop: -2 }} />
                        添加分类
                    </div>
                </SubMenu>
                {
                    selected === 'sub2'
                        ? <Menu.Item key='sub2' icon={<img src={tit2} style={menu_img} />} style={{ ...menu, backgroundColor: 'rgb(101,153,255)' }}
                        ><Link to='/home/aa'>友情链接</Link></Menu.Item>
                        : <Menu.Item key='sub2' icon={<img src={tit2} style={menu_img} />} style={menu}
                            onClick={(props) => {
                                setSelected(props.key)
                                // props.dispatch(getFls());
                            }}
                        ><Link to='/home/aa'>友情链接</Link></Menu.Item>
                }
                {
                    selected === 'sub3'
                        ? <Menu.Item key='sub3' style={{ ...menu, backgroundColor: 'rgb(101,153,255)', }}
                            onClick={() => { setVisible(true) }}
                        ><img src={tit3} style={menu_img} />版权信息</Menu.Item>
                        : <Menu.Item key='sub3' style={menu}
                            onClick={(props) => {
                                setSelected(props.key)
                                setVisible(true)
                            }}
                        ><img src={tit3} style={menu_img} />版权信息</Menu.Item>
                }
                {/* 更新菜单项名 */}
                <Modal
                    centered
                    visible={visible3}
                    onOk={() => {
                        props.dispatch(updateMenuName(selected, newMenu))
                        setVisible3(false)
                    }}
                    onCancel={() => setVisible3(false)}
                    width={500}
                    cancelText='取消'
                    okText='确定'
                    maskClosable={false}
                    closable={false}
                    destroyOnClose={true}
                >
                    <p style={{ marginBottom: 3 }}>请输入</p>
                    <Input onChange={e => { setNewMenu(e.target.value) }}></Input>
                </Modal>
                {/* 版权信息 */}
                <Modal
                    centered
                    visible={visible}
                    onOk={() => {
                        updateCp(newcp)
                        setVisible(false)
                    }}
                    onCancel={() => setVisible(false)}
                    width={500}
                    cancelText='取消'
                    okText='确定'
                    maskClosable={false}
                    closable={false}
                    destroyOnClose={true}
                >
                    <p style={{ marginBottom: 3 }}>请输入版权信息</p>
                    <Input onChange={e=>setNewcp(e.target.value)}></Input>
                </Modal>
                {/* 上传分类 */}
                <Modal
                    centered
                    visible={visible2}
                    onOk={() => {
                        props.dispatch(addMenu(menuName, menuLogo, props.menus.length + 1))
                        setVisible2(false)
                    }}
                    onCancel={() => setVisible2(false)}
                    width={300}
                    cancelText='取消'
                    okText='确定'
                    maskClosable={false}
                    closable={false}
                    destroyOnClose={true}
                >
                    <p style={{ marginBottom: 3 }}>请输入导航分类名称</p>
                    <Input onChange={e => setMenuName(e.target.value)}></Input>
                    <p></p>
                    <p style={{ marginBottom: 3 }}>请上传图标</p>
                    <Upload {...p}>
                        <Button
                            style={{ width: 252 }}
                            icon={<img src={img} style={{ width: 20, marginTop: -4, marginRight: 5 }} />}
                        >Click to Upload</Button>
                    </Upload>
                </Modal>
            </Menu>
        </Sider>
        <Layout style={{ marginLeft: 200 }}>
            <Content
                style={{
                    overflow: 'initial',
                    backgroundColor: '#fff'
                }}
            >
                <Switch>
                    <Route path='/home/cm/:pageid' component={ContentManagement}></Route>
                    <Route path='/home/aa' component={FriendLinkfrom}></Route>
                    <Route path='/home/bb' component={Modal2}></Route>
                    {/* <Redirect to={`/home/cm/${props.menus[0].pageid}`} /> */}
                </Switch>
            </Content>
        </Layout>
    </Layout >
};

const menu = {
    fontSize: 17,
    fontWeight: 'bold',
    letterSpacing: 3,
    backgroundColor: 'rgb(42,42,42)'
}
const menu_img = {
    width: 20,
    height: 20,
    marginTop: -3,
    marginRight: 5
}
const menu_icon = {
    width: 13,
    marginTop: -2,
    marginLeft: 10
}
const btn = {
    width: 50,
    height: 20,
    color: '#000'
}
const mapStateToProps = state => ({
    menus: state.reducer.menus,
    login: state.reducer.login
})
export default connect(mapStateToProps)(Home)
