import React, { useEffect } from 'react'
import bgimg from '../../image/登录页面背景图2.png'
import logo from '../../image/运营文件夹logo.png'
import { Input, Button, Form, Checkbox } from 'antd'
import { getCode } from '../../store/actionCreator'
import { connect } from 'react-redux'
import './index.css'
import CryptoJs from 'crypto-js'
const Login = (props) => {
    const isRmb = localStorage.getItem('rmb') === 'true';
    const userinfo = localStorage.getItem('userinfo') ? localStorage.getItem('userinfo').split(',') : [];
    let deui = userinfo.map(item => {
        return CryptoJs.AES.decrypt(item, 'qwert').toString(CryptoJs.enc.Utf8);
    })
    let initui = isRmb ? { admin: deui[0], password: deui[1] } : {};
    console.log(deui)

    useEffect(() => {
        props.dispatch(getCode())
    }, [])
    const onFinish = (values) => {
        const key = 'qwert';
        let admin = values.admin;
        let pw = values.password;
        let code = values.code;
        let rmb = values.remember;
        localStorage.setItem('rmb', rmb);
        let eadmin = CryptoJs.AES.encrypt(admin, key).toString();
        let epw = CryptoJs.AES.encrypt(pw, key).toString();
        if (code.toLowerCase() !== props.codetext.toLowerCase()) {
            alert('验证码错误')
            props.dispatch(getCode())
            return;
        }
        fetch('/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                admin: eadmin,
                pw: epw
            })
        })
            .then(res => res.json())
            .then(res => {
                if (res.msg) {
                    props.history.replace('/home', { test: false })
                    localStorage.setItem('token', res.data);
                    if (rmb) {
                        localStorage.setItem('userinfo', [eadmin, epw]);
                    }
                } else {
                    alert(res.data)
                    props.dispatch(getCode())
                }
            })
    };
    return (
        <div style={{ backgroundImage: `url(${bgimg})`, height: '100%', width: '100%', paddingTop: '5%' }}>
            <img src={logo} style={{ width: 80, margin: '0 auto', display: 'block', marginBottom: 10 }} />
            <h2 style={{ textAlign: 'center', color: '#fff', fontWeight: 'bold', letterSpacing: 5 }}>运营文件夹后台管理系统</h2>
            <div
                style={{ width: 390, padding: 20, margin: '20px auto 0', border: '1px dashed #fff' }}
            >
                <Form
                    name="login"
                    initialValues={{
                        ...initui,
                        remember: true
                    }}
                    onFinish={onFinish}
                >
                    <Form.Item
                        name="admin"
                        rules={[
                            {
                                required: true,
                                message: '请输入账号！',
                            },
                        ]}
                    >
                        <Input placeholder="账号" />
                    </Form.Item>
                    <Form.Item
                        name="password"
                        rules={[
                            {
                                required: true,
                                message: '请输入密码！',
                            },
                        ]}
                    >
                        <Input type="password" placeholder="密码" />
                    </Form.Item>
                    <Form.Item
                        name="code"
                        rules={[
                            {
                                required: true,
                                message: '请输入验证码！',
                            },
                        ]}
                        style={{ width: '50%', float: 'left' }}
                    >
                        <Input placeholder="验证码" maxLength={4} />
                    </Form.Item>
                    <div dangerouslySetInnerHTML={{ __html: props.code }}
                        style={{ float: 'right' }}
                        onClick={() => {
                            props.dispatch(getCode())
                        }}></div>
                    <Form.Item style={{ display: 'block', marginBottom: 5 }}>
                        <Button type="primary" htmlType="submit" style={{ width: '100%' }}>
                            登录
                    </Button>
                    </Form.Item>
                    <Form.Item style={{ marginBottom: 0 }}>
                        <Form.Item name="remember" valuePropName={isRmb ? "checked" : "none"} noStyle>
                            <Checkbox style={{ color: '#fff' }}>记住密码</Checkbox>
                        </Form.Item>
                        <a href="" style={{ float: 'right', color: '#fff' }}>
                            忘记密码
                    </a>
                    </Form.Item>
                </Form>

            </div>
        </div>
    )
}
const mapStateToProps = state => ({
    code: state.reducer.code,
    codetext: state.reducer.codetext,
})
export default connect(mapStateToProps)(Login)
