import React, { useState, useEffect } from 'react'
import { Input, Button, Modal } from 'antd';
import add from '../../../image/添加导航信息图标.png'
import sort from '../../../image/排序图标.png'
import del from '../../../image/删除图标.png'
import img from '../../../image/上传图标图标.png'
import { ExclamationCircleOutlined } from '@ant-design/icons'
import { connect } from 'react-redux'
import { ReactSortable } from "react-sortablejs";
import { getFls, addFl, delFl, updateFls } from '../../../store/actionCreator'

// import 
const FriendLink = (props) => {
    const [state, setState] = useState([]);
    const [handle, setHandle] = useState('false');
    const [fd1, setFd1] = useState('');
    const [fd2, setFd2] = useState('');
    function confirm(flid) {
        Modal.confirm({
            title: 'Confirm',
            icon: <ExclamationCircleOutlined />,
            content: '删除此项？',
            okText: '确认',
            cancelText: '取消',
            onOk: () => {
                props.dispatch(delFl(flid));
                props.dispatch(getFls());
                setState(props.fls);
            }
        });
    }
    useEffect(() => {
        props.dispatch(getFls());
    }, [])
    useEffect(() => {
        setState(props.fls);
    }, [props.fls])
    return (
        <div style={{}}>
            {/* 标题栏 */}
            <div style={{ height: 50, backgroundColor: 'rgb(101,153,255)' }}>
                <div style={{ width: global.tpa1(1611), margin: '0 auto', paddingTop: 5 }}>
                    <div style={{ ...title, width: '8.5%' }}>排序</div>
                    <div style={{ ...title, width: '12%' }}>标题</div>
                    <div style={{ ...title, width: '23%' }}>链接</div>
                    <div style={{ ...title, width: '6.5%' }}>提交</div>
                    <div style={{ ...title, width: '7%' }}>删除</div>
                </div>
            </div>
            <div style={{ width: global.tpa1(1611), backgroundColor: '#fff', margin: '0 auto' }}>
                <div style={{ paddingTop: 15 }}>
                    {/* 添加图标 */}
                    <div style={{ width: '8.5%', paddingBottom: 15, display: 'inline-block' }}>
                        <img src={add} style={{ width: 20, margin: '0 auto', display: 'block' }}
                            onClick={() => {
                                console.log('添加一行')
                                setState([...state, {}])
                            }}
                        />
                    </div>
                    <div style={{ float: 'right', display: 'inline-block' }}>
                        <Button style={sortBtn}
                            onClick={() => {
                                setHandle('handle')
                                setState(props.fls);
                            }}
                        >排序</Button>
                        <Button style={sortBtn}
                            onClick={() => {
                                setHandle('false')
                                const sortTables = [];
                                state.map((item, i) => {
                                    sortTables.push({ flid: item.flid, sortid: i + 1 })
                                })
                                sortTables.sort((a, b) => a.menuid - b.menuid)
                                console.log(sortTables)
                                props.dispatch(updateFls(sortTables))
                            }}
                        >完成</Button>
                    </div>
                </div>
                <ReactSortable
                    list={state}
                    setList={setState}
                    group="groupName"
                    animation={200}
                    delayOnTouchStart={true}
                    delay={2}
                    handle={'.handle'}
                >
                    {
                        state.map((item) => {
                            return item.flid
                                ? <div style={{ ...list, paddingLeft: global.tpa2(44) }} key={item.id}>
                                    <img src={sort} style={{ width: 30, marginRight: global.tpa2(44), position: 'relative', top: -12 }} className={handle} />
                                    <div style={{ ...form, width: global.tpa2(191), paddingTop: 3, overflowY: 'auto' }}><p>{item.title}</p></div>
                                    <div style={{ ...form, width: global.tpa2(362), paddingTop: 3, overflowY: 'auto' }}><p>{item.link}</p></div>
                                    <Button style={{ ...form2, width: global.tpa2(82), textAlign: 'center', backgroundColor: 'rgb(101,153,255)', color: '#fff', position: 'relative', top: -10 }} disabled>
                                        已提交
                                    </Button>
                                    <img src={del}
                                        style={{ width: 25, marginLeft: global.tpa2(44), position: 'relative', top: -12 }}
                                        onClick={() => {
                                            confirm(item.flid)
                                        }}
                                    />
                                </div>
                                : <div style={{ ...list, paddingLeft: global.tpa2(44) }} key={item.id}>
                                    <img src={sort} style={{ width: 30, marginRight: global.tpa2(44) }} className='false' />
                                    <Input
                                        onChange={e => setFd1(e.target.value)}
                                        style={{ ...form2, width: global.tpa2(191), }} />
                                    <Input
                                        onChange={e => setFd2(e.target.value)}
                                        style={{ ...form2, width: global.tpa2(362) }} />

                                    <Button style={{ ...form2, width: global.tpa2(82), textAlign: 'center', backgroundColor: 'rgb(101,153,255)', color: '#fff' }}
                                        onClick={() => {
                                            props.dispatch(addFl(fd1, fd2, props.fls.length + 1))
                                        }}
                                    >提交</Button>
                                    <img src={del} style={{ width: 25, marginLeft: global.tpa2(44) }}
                                        onClick={() => {
                                            setState(state.slice(0, state.length - 1))
                                        }}
                                    />
                                </div>
                        })
                    }
                </ReactSortable>
            </div>
        </div >
    )
}
const sortBtn = {
    display: 'inline-block',
    backgroundColor: 'rgb(101,153,255)',
    color: '#fff',
    marginLeft: 10
}
const list = {
    backgroundColor: '#F2F2F2',
    // paddingLeft: global.tpa2(44),
    marginBottom: 5,
    height: 60,
    paddingTop: 14,
    borderRadius: 5
}
const title = {
    display: 'inline-block',
    color: '#fff',
    fontStyle: 'SourceHanSansCN',
    fontWeight: 'bold',
    textAlign: 'center',
    lineHeight: 3
}
const form = {
    margin: '0 0.6%',
    height: 30,
    padding: 0,
    display: 'inline-block',
    backgroundColor: '#fff',
    border: '1px solid #ccc',
    boxSizing: 'border-box'
}
const form2 = {
    margin: '0 0.6%',
    height: 30,
    padding: 0,
}
const mapStateToProps = (state) => ({
    contentList: state.reducer.menus,
    table: state.reducer.table,
    fls: state.reducer.fls
})

export default connect(mapStateToProps)(FriendLink)
