// 获取内容列表
let url = 'http://192.168.8.145:8080';
const getMenus = () => {
    return dispatch => {
        fetch('/getmenus', {
            method: 'GET',
            headers: {
                'Content-type': 'application/json'
            }
        })
            .then(res => res.json())
            .then(res => {
                return dispatch({
                    type: 'MENUS',
                    menus: res.data
                })
            })
    }
}
const getTable = (pageid) => {
    return dispatch => {
        fetch('/gettable/' + pageid, {
            method: 'GET',
            headers: {
                'Content-type': 'application/json'
            }
        })
            .then(res => res.json())
            .then(res => {
                return dispatch({
                    type: 'TABLE',
                    table: res.data
                })
            })
    }
}
