// 菜单排序更新
const updateMenus = sortMenus => {
    return dispatch => {
        fetch('/updatemenus', {
            method: 'post',
            headers: {
                'Content-type': 'application/json'
            },
            body: JSON.stringify({
                sortMenus: sortMenus
            })
        })
            .then(res => res.json())
            .then(res => {
                return dispatch({
                    type: 'MENUS',
                    menus: res.data
                })
            })
    }
}
