// 更新菜单名
const updateMenuName = (selected, newMenu) => {
    console.log(newMenu)
    return dispatch => {
        fetch(`/updatemenuname?menuid=${selected}&&newMenu=${newMenu}`, {
            method: 'get',
            headers: {
                'content-type': 'application/json'
            }
        })
            .then(res => res.json())
            .then(res => {
                // console.log(res)
                return dispatch({
                    type: 'MENUS',
                    menus: res.data
                })
            })
    }
}
// 添加菜单项
const addMenu = (menuName, menuLogo, sortId) => {
    console.log(menuName, menuLogo)
    return dispatch => {
        fetch('/addmenu', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                menuName: menuName,
                menuLogo: menuLogo,
                sortId: sortId
            })
        })
            .then(res => res.json())
            .then(res => {
                console.log(res.data)
                return dispatch({
                    type: 'MENUS',
                    menus: res.data
                })
            })
    }
}
// 添加表格项
const addTable = (pageid, title, brief, logo, link, sortid) => {
    return dispatch => {
        fetch('/addtable', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                pageid: pageid,
                title: title,
                brief: brief,
                logo: logo,
                link: link,
                sortid: sortid
            })
        })
            .then(res => res.json())
            .then(res => {
                return dispatch({
                    type: 'TABLE',
                    table: res.data
                })
            })
    }
}
