const login = (admin, pw) => {
    console.log(admin, pw)
    return dispatch => {
        fetch('/login', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                admin: admin,
                pw: pw
            })
        })
            .then(res => res.json())
            .then(res => {
                console.log(res)
                if(res.msg){
                    localStorage.setItem('token', res.data);
                }
                return dispatch({
                    type: 'LOGIN',
                    login: res.msg
                })
            })
    }
}
const updateCp = newCp => {
    fetch('/updatecp', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        body: JSON.stringify({
            newCp: newCp
        })
    })
        .then()
        .then()
}
const getCode = () => {
    return dispatch => {
        fetch('/getcode', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json'
            }
        })
            .then(res => res.json())
            .then(res => {
                console.log(res)
                console.log(res.text)
                return dispatch({
                    type: 'CODE',
                    code: res.data,
                    text: res.text
                })
            })
    }
}

export {
    getMenus, updateMenus, delMenu, updateMenuName, addMenu,
    getTable, updateTables, addTable, delTable,
    getFls, updateFls, addFl, delFl,
    login, updateCp, getCode
}